@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737373}.container{max-width:1280px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality/static/media/bg.b236af76.jpg);background-size:cover;padding:80px 0 178px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:45px}.hero-navigation-item{text-decoration:none;color:#FFFFFF;margin-left:74px;font-weight:normal;font-size:19px;line-height:22px}.hero-navigation-item:first-child{margin-left:0}.hero-title{font-weight:normal;font-size:57px;line-height:67px;color:#FFFFFF;margin:108px 0 133px;width:664px}.hero-link{display:inline-block;background:#B78A3F;color:#FFFFFF;padding:35px 151px;font-weight:normal;font-size:32px;line-height:37px}

.facts{padding:100px 60px 50px;position:relative;box-sizing:border-box}.facts-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#FFFFFF;margin-bottom:30px}.facts-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#ffffff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:flex;justify-content:center}.facts-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #878A3F;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.sectors{padding:50px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#ffffff;text-align:center;margin-bottom:42px}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#ffffff;text-align:justify;margin-bottom:30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{width:calc((100% / 3) - 70px);height:240px;margin-bottom:30px;position:relative;display:flex;align-items:center}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{left:100%;width:440px;position:absolute;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);background-color:#DADADA;padding:16px 16px 16px 75px;z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}50%{transform:rotate(30deg)}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}50%{transform:rotate(30deg)}100%{opacity:1}}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#ffffff}.air-q-map{height:560px;margin:42px 0}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;color:#ffffff}

.footer{border-top:1px solid #ffffff}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between;margin-bottom:80px}.footer-nav{display:flex;flex-direction:column}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#ffffff;margin-bottom:20px}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#ffffff;text-align:justify}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#ffffff;margin-bottom:14px}.text-yellow{color:#EDE852}

